import React, {Component} from 'react';
import {Helmet} from 'react-helmet'
import Slider from 'react-slick';
import '../common/index.scss';
import {connect} from "react-redux";

// import custom Components
import Service from "./common/service";
import BrandBlock from "./common/brand-block";
import NewProduct from "../common/new-product";
import Breadcrumb from "../common/breadcrumb";
import DetailsWithPrice from "./common/product/details-price";
import DetailsTopTabs from "./common/details-top-tabs";
import { addToCart, addToCartUnsafe, addToWishlist } from '../../actions'
import ImageZoom from './common/product/image-zoom'
import SmallImages from './common/product/small-image'
import {Redirect} from "react-router-dom";



class LeftSideBar extends Component {

    constructor() {
        super();
        this.state = {
            open:false,
            variant: 0,
            size: 0,
            quantity: 1,
            nav1: null,
            nav2: null
        };
    }

    // document.getElementById('idOfElement').classList.add('newClassName');


    componentDidMount() {
        this.setState({
            nav1: this.slider1,
            nav2: this.slider2
        });
        console.log(this.props)
    }

    filterClick() {
        document.getElementById("filter").style.left = "-15px";
    }
    backClick() {
        document.getElementById("filter").style.left = "-365px";
    }

    renderVariants = () => {
        const item = this.props.item;
        const imgBuilder = []
        const uniqueImages = []
        try {
            if (item.variants.length) {
                for(let i=0; i < item.variants.length; i++) {
                    if (uniqueImages.indexOf(item.variants[i].image.src) === -1) {
                        uniqueImages.push(item.variants[i].image.src)
                        const temp = <div  key={i}>
                            <ImageZoom onClick={() => console.log("hit me")} image={item.variants[i].image.src} />
                        </div>
                        imgBuilder.push(temp)
                    }
                }
            }
            else {
                if (item.images.length) {
                    for(let i=0; i < item.images.length; i++) {
                        if (uniqueImages.indexOf(item.images[i].image.src) === -1) {
                            uniqueImages.push(item.images[i].image.src)
                            const temp = <div key={i}>
                                <ImageZoom onClick={() => console.log("hit me")} image={item.images[i].image.src}/>
                            </div>
                            imgBuilder.push(temp)
                        }
                    }
                }
            }
            return imgBuilder;
        }
        catch {
            return []
        }
    }

    handleAddToCart = (item) => {
        const product = item;
        product.selectedVariant = this.state.variant;
        product.selectedSize = this.state.size;

        this.props.addToCart(product, this.state.quantity, this.props.client)
    }

    render(){
        const {symbol, item, addToCart, addToCartUnsafe, addToWishlist} = this.props
        var products = {
            slidesToShow: 1,
            slidesToScroll: 1,
            dots: false,
            arrows: true,
            fade: true
        };
        var productsnav = {
            slidesToShow: 3,
            swipeToSlide:true,
            arrows: false,
            dots: false,
            focusOnSelect: true
        };

        if (!item) {
            return <Redirect to={"/home"}/>
        }

        return (
            <div>
                {/*SEO Support*/}
                <Helmet>
                    { item ?
                        <title>Pretty Girl Store | {item.productType} | {item.title}</title>
                        :
                        <title>Pretty Girl Store</title>
                    }

                    <meta name="description" content="Pretty Girl have offered incredible designs, stylish clothes, and magnificent modern looks for the budget minded fashionista. Serving our adoring customers in any one of our 25 Pretty Girl Stores throughout the Tri-State area, Pretty Girl prides itself on taking fast fashion to the next level at incredibly low prices." />
                </Helmet>
                {/*SEO Support End */}

                <Breadcrumb  parent={'Product'} title={item.title} />

                {/*Section Start*/}
                {(item)?
                <section className="section-b-space">
                    <div className="collection-wrapper">
                        <div className="container">
                            <div className="row">

                                <div className="col-sm-3 collection-filter" id="filter">
                                    <div  className="collection-mobile-back pl-5">
                                        <span onClick={this.backClick}  className="filter-back">
                                            <i className="fa fa-angle-left" aria-hidden="true"></i> back
                                        </span>
                                    </div>

                                    {/* <BrandBlock/> */}
                                    <Service/>
                                    {/*side-bar single product slider start*/}
                                    <NewProduct/>
                                    {/*side-bar single product slider end*/}
                                </div>
                                <div className="col-lg-9 col-sm-12 col-xs-12">
                                    <div className="">
                                        <div className="row">
                                            <div className="col-xl-12">
                                                <div className="filter-main-btn mb-2">
                                                    <span onClick={this.filterClick}  className="filter-btn" >
                                                        <i className="fa fa-filter" aria-hidden="true"></i> filter</span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-lg-6 product-thumbnail">
                                                <Slider {...products} asNavFor={this.state.nav2} ref={slider => (this.slider1 = slider)} className="product-slick">
                                                    {
                                                        this.renderVariants()
                                                    }
                                                </Slider>
                                                <SmallImages variant={this.state.variant} setVariant={(index) => this.setState({variant: index})} item={item} settings={productsnav} navOne={this.state.nav1} />
                                            </div>
                                            <DetailsWithPrice setSize={(size) => this.setState({size: size})} size={this.state.size} quantity={this.state.quantity} setQuantity={(quantity) => this.setState({quantity: quantity})} symbol={symbol} item={item} navOne={this.state.nav1} addToCartClicked={this.handleAddToCart} BuynowClicked={addToCartUnsafe} addToWishlistClicked={addToWishlist} />
                                        </div>
                                    </div>
                                    {/*<DetailsTopTabs item={item} />*/}
                                </div>
                            </div>
                        </div>
                    </div>
                </section> : ''}
                {/*Section End*/}
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    let productId = ownProps.match.params.id;
    return {
        item: state.data.products.find(el => el.id == productId),
        symbol: state.data.symbol,
        client: state.client
    }
}

export default connect(mapStateToProps, {addToCart, addToCartUnsafe, addToWishlist}) (LeftSideBar);
