import React, {Component, useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';

import CartPage from '../components/common/headers/common/cart-header'
import {removeFromCart} from '../actions'
import {getCartTotal} from '../services'

const CartContainer = ({cartList, total, symbol, removeFromCart, client}) => {
    useEffect(() => {
        //component will mount
        return () => {
            //component will unmount
        };
    }, [total]);

   const createCheckout = () => {
       const checkoutID = localStorage.getItem("checkout")
        if (checkoutID) {
            client.client.checkout.fetch(checkoutID).then((checkout) => {
                // Do something with the checkout
                window.open(checkout.webUrl)
            });
        }
   }

    return (
        <li  className="onhover-div mobile-cart"><div className="cart-qty-cls">{cartList.products.length}</div>
            <div>
                <img src={`${process.env.PUBLIC_URL}/assets/images/icon/cart.png`} className="img-fluid" alt=""/>
                <i className="fa fa-shopping-cart"></i>
                <ul className="show-div shopping-cart">
                    { cartList.products.map((item,index) => (
                        <CartPage key={index} item={item} total={total} symbol={symbol} removeFromCart={() => removeFromCart(item, client)}  />
                    ))}
                    {(cartList.products.length > 0) ?
                        <div>
                            <li>
                                <div className="total">
                                    <h5>subtotal : <span>{symbol}{cartList.sum}</span></h5>
                                </div>
                            </li>
                            <li>
                                <div className="buttons">
                                    {/*<Link to={`${process.env.PUBLIC_URL}/cart`} className="view-cart">view cart</Link>*/}
                                    <p onClick={createCheckout} className="checkout">Checkout</p>
                                </div>
                            </li></div>
                        :
                        <li><h5>Your cart is currently empty.</h5></li>}
                </ul>
            </div>
        </li>
    )
}


function mapStateToProps(state) {
    return {
        cartList: state.cartList,
        symbol: state.data.symbol,
        client: state.client
    }
}

export default connect(mapStateToProps, {removeFromCart})(CartContainer);
