import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import Modal from 'react-responsive-modal';
import moment from "moment";
import brokenImg from '../../../assets/images/portfolio/7.jpg'

class ProductListItem extends Component {

    constructor(props){
        super(props)

        this.state = {
            open: false,
            stock: 'In Stock',
            quantity: 1,
            image: null
        }
    }

    onOpenModal = () => {
        this.setState({ open: true });
    };

    onCloseModal = () => {
        this.setState({ open: false });
    };

    onClickHandle(img) {
        this.setState({ image : img} );
    }

    minusQty = () => {
        if(this.state.quantity > 1) {
            this.setState({stock: 'In Stock'})
            this.setState({quantity: this.state.quantity - 1})
        }
    }

    plusQty = () => {
        if(this.props.product.stock >= this.state.quantity) {
            this.setState({quantity: this.state.quantity+1})
        }else{
            this.setState({stock: 'Out of Stock !'})
        }
    }
    changeQty = (e) => {
        this.setState({ quantity: parseInt(e.target.value) })
    }

    renderVariants = (product) => {
        const variantList = [];
        const uniqueVariants = [];
            for (let i=0; i < product.variants.length; i++) {
                try {
                    if (uniqueVariants.length > 3) break;

                    if (product.variants[i].image) {
                        if (uniqueVariants.indexOf(product.variants[i].image.src) === -1) {
                            uniqueVariants.push(product.variants[i].image.src)
                        } else break;

                        if (product.variants.length === 1) {
                            break;
                        } else {
                            const temp = (
                                <li className={`grid_thumb_img ${(product.variants[i].image.src === this.state.image) ? 'active' : ''}`}
                                    key={i}>
                                    <a href="javascript:void(0)" title="Add to Wishlist">
                                        <img src={`${product.variants[i].image.src}`}
                                             onClick={() => this.onClickHandle(product.variants[i].image.src)}/>
                                    </a>
                                </li>
                            )
                            variantList.push(temp);
                        }
                    }

                }
            catch {
                    console.log("error getting variant picture")
                    if (product)
                        if (product.title)
                            console.log("check item : " + product.title);
                }
            }

        return variantList

    }

    returnMainIMG = (product) => {
        if (!this.state.image) {
            if (product.variants.length) {
                try {
                    if (product.variants[0].image.src) {
                        return product.variants[0].image.src
                    }
                    else {
                        try {
                            if (product.images[0].src) {
                                return product.images[0].src
                            }
                        }
                        catch {
                            console.log("item " + product.title + " has no image or variant image. rendering nothing")
                            return ""
                        }
                    }
                }
                catch {
                    return brokenImg;
                }

            }
        }
        else {
            return this.state.image;
        }
    }

    checkIfNew = () => {
        try {
            const createdAt = moment(Date.now());
            const difference = createdAt.diff(this.props.product.createdAt, "hours");
            if (difference < 4350)
                return (<span style={{zIndex:9999}} className="lable3">new</span>);
            else return "";
        }
        catch (e){
            console.log(e);
        }
    }

    checkIfSale = () => {
        try {
            const salePrice = this.props.product.variants[0].price;
            const fullPrice = this.props.product.variants[0].compareAtPrice;

            if (!fullPrice) return "";

            const discount = (100 - (salePrice/fullPrice * 100) );

            if (discount > 5)
                return (<div style={{position:"absolute", top:0, left:0, right:0,backgroundColor:"purple", borderBottomLeftRadius:"10px", borderBottomRightRadius:"10px"}}>
                    <p style={{textAlign:"center", marginTop:5}}>{Math.round(discount)}% off!</p>
                </div>
                )
        }
        catch(e) {
            console.log(e);
        }
    }




    render() {

        const {product, symbol, onAddToCartClicked, onAddToWishlistClicked, onAddToCompareClicked} = this.props;
        const {open} = this.state;

            let RatingStars = []
            for(var i = 0; i < product.rating; i++) {
                RatingStars.push(<i className="fa fa-star" key={i}></i>)
            }

        return (

                    <div className="product-box" style={{display:"flex", flexDirection: "column", justifyContent:"flex-end"}}>
                        <div className="img-wrapper">
                            <div className="lable-block" style={{position:"relative"}}>
                                {this.checkIfNew(product)}
                                {this.checkIfSale(product)}

                            </div>
                            <div className="front">
                                <Link to={`${process.env.PUBLIC_URL}/product/${product.id}`} ><img
                                    src={`${this.returnMainIMG(product)}`}
                                    className="img-fluid"
                                    alt="" /></Link>
                            </div>
                            <div className="cart-info cart-wrap">
                                <button title="Add to cart" onClick={() => onAddToCartClicked(product, 1)}>
                                    <i className="fa fa-shopping-cart" aria-hidden="true"></i>
                                </button>
                                <a href="javascript:void(0)" title="Add to Wishlist" onClick={onAddToWishlistClicked} >
                                    <i className="fa fa-heart" aria-hidden="true"></i>
                                </a>
                                <a href="javascript:void(0)" data-toggle="modal"
                                   data-target="#quick-view"
                                   title="Quick View"
                                   onClick={this.onOpenModal}><i className="fa fa-search" aria-hidden="true"></i></a>
                                <Link to={`${process.env.PUBLIC_URL}/compare`} title="Compare" onClick={onAddToCompareClicked}>
                                    <i className="fa fa-refresh" aria-hidden="true"></i></Link>
                            </div>
                            {product.variants.length ?
                                <ul className="product-thumb-list">
                                    {this.renderVariants(product)}
                                </ul>
                                :
                                <ul className="product-thumb-list">
                                </ul>
                            }

                        </div>
                        <div className="product-detail">
                            <div>
                                <div className="rating">
                                    {RatingStars}
                                </div>
                                <Link to={`${process.env.PUBLIC_URL}/product/${product.id}`}>
                                    <h6 style={{textOverflow:"ellipsis", overflow:"hidden", whiteSpace:"nowrap"}}>{product.title}</h6>
                                </Link>
                                <h4>{symbol}{product.variants[0].price}
                                    {
                                        product.variants[0].compareAtPrice &&
                                        <span className="money strikethrough">{symbol}{product.variants[0].compareAtPrice}</span>
                                    }

                                </h4>
                            </div>
                        </div>
                    <Modal open={open} onClose={this.onCloseModal} center>
                            <div className="modal-dialog modal-lg modal-dialog-centered" role="document">
                                <div className="modal-content quick-view-modal">
                                    <div className="modal-body">
                                        <div className="row">
                                            <div className="col-lg-6  col-xs-12">
                                                <div className="quick-view-img">
                                                    <img src={product.variants?
                                                        this.state.image?this.state.image:product.variants[0].images
                                                        :product.pictures[0]} alt="" className="img-fluid" />
                                                </div>
                                            </div>
                                            <div className="col-lg-6 rtl-text">
                                                <div className="product-right">
                                                    <h2> {product.name} </h2>
                                                    <h3>{symbol}{product.salePrice}
                                                        <del><span className="money">{symbol}{product.price}</span></del>
                                                    </h3>
                                                    {product.variants?
                                                    <ul className="color-variant">
                                                        {product.variants.map((vari, i) =>
                                                            <li className={vari.color} key={i} title={vari.color} onClick={() => this.onClickHandle(vari.images)}></li>)
                                                        }
                                                    </ul>:''}
                                                    <div className="border-product">
                                                        <h6 className="product-title">product details</h6>
                                                        <p>{product.shortDetails}</p>
                                                    </div>
                                                    <div className="product-description border-product">
                                                        {product.size?
                                                        <div className="size-box">
                                                            <ul>
                                                                {product.size.map((size, i) => {
                                                                    return <li key={i}><a href="#">{size}</a></li>
                                                                })}
                                                            </ul>
                                                        </div>:''}
                                                        <h6 className="product-title">quantity</h6>
                                                        <div className="qty-box">
                                                            <div className="input-group">
                                                              <span className="input-group-prepend">
                                                                <button type="button" className="btn quantity-left-minus" onClick={this.minusQty} data-type="minus" data-field="">
                                                                 <i className="fa fa-angle-left"></i>
                                                                </button>
                                                              </span>
                                                                <input type="text" name="quantity" value={this.state.quantity}  onChange={this.changeQty} className="form-control input-number" />
                                                                <span className="input-group-prepend">
                                                                <button type="button" className="btn quantity-right-plus" onClick={this.plusQty} data-type="plus" data-field="">
                                                                <i className="fa fa-angle-right"></i>
                                                                </button>
                                                               </span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="product-buttons">
                                                        <button  className="btn btn-solid" onClick={() => onAddToCartClicked(product, this.state.quantity)} >add to cart</button>
                                                        <Link to={`${process.env.PUBLIC_URL}/product/${product.id}`} className="btn btn-solid">view detail</Link>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                    </Modal>
                </div>
        )
    }
}

export default ProductListItem;
