import {
    UPDATE_CART } from "../constants/ActionTypes";


export default function cartReducer(state = {
    products: [],
    sum: null

}, action) {
    switch (action.type) {
        case UPDATE_CART:
            const product = action.product
            const newCart = {
                products: product.lineItems,
                sum: product.lineItemsSubtotalPrice.amount
            }
            console.log("--------------------------------")
            console.log(state)
            console.log("--------------------------------")
            return newCart;

        default:
    }
    return state;
}
